<template>
    <div class="main">
        <!--  -->
        <div class="panel panel-default">
            <div class="panel-heading">
                <span class="title" v-text="topic.title"></span>
                <div class="changes">
                    <ul>
                        <li><span class="dote"> <span v-text="topic.last_reply_at"></span></span></li>
                        <li v-for="item in topic" v-if="item.loginname">
                            <span class="dote"><span v-text="item.loginname"></span></span></li>
                        <li><span class="dote" v-text="topic.visit_count"></span> </li>
                        <li><span class="dote"><span v-text="topic.tab=='share'?'':(topic.tab=='ask'?'':(topic.tab=='job'?'':'weex'))"></span></span> 
                        </li>
                    </ul>
                </div>
            </div>
            <div class="panel-body" v-html="topic.content">
            </div>
        </div>
        <!--  -->
        <div class="panel panel-default">
            <!--  -->
            <div class="panel-heading" v-text="''">
                
            </div>
            <!--  -->
            <div class="panel-body">
                <!--  -->
                <div class="reply" v-for="item,index in topic.replies">
                    <!--  -->
                    <div class="reply_author">
                        <!--  -->
                        <a href="">
                            <img class="img_src" :src="item.author.avatar_url" alt="">
                        </a>
                        <!--  -->
                        <!-- <a href="" v-text="item.author.loginname"></a> -->
                        <!--  -->
                        <a href="">
                            <span v-text="item.create_at"></span>
                            <span style="font-size:20px; height:20px;"></span>
                            <span>{{index}}</span>
                        </a>
                        <!--  -->
                        <span v-if="item.ups.length!=0">
                            <i class="glyphicon glyphicon-thumbs-up zan"></i>
                            <span v-text="item.ups.length"></span>
                        </span>
                    </div>
                        
                    <!--  -->
                    <div class="reply_content" v-html="item.content"></div>
                </div>

            </div>
        </div>
    </div>
</template>
<script>
	import axios from 'axios';
    export default {
    	data () {
    		return{
    			url : '',
    			topic : {}
    		}
    	},
    	created:function(){
    		this.url = this.$route.fullPath;
    		var vthis = this;
    		this.$http.get(this.url)
    		.then(function(data){
    			vthis.topic = data.data.data;
                let dateTime = Math.floor((new Date()-Date.parse(vthis.topic.last_reply_at))/1000);
               // console.log(dateTime);
                if(dateTime<60){
                    vthis.topic.last_reply_at= Math.floor(dateTime)+'';
                }else if(dateTime/60>1&&dateTime/60/60<1){
                    vthis.topic.last_reply_at=Math.floor(dateTime/60)+'';
                }else if(dateTime/60/60>1&&dateTime/60/60/24<1){
                    vthis.topic.last_reply_at=Math.floor(dateTime/60/60)+'';
                }else if(dateTime/60/60/24>1&&dateTime/60/60/24/30<1){
                    vthis.topic.last_reply_at=Math.floor(dateTime/60/60/24)+'';
                }else if(dateTime/60/60/24/30>1){
                    vthis.topic.last_reply_at=Math.floor(dateTime/60/60/24/30)+'';
                };              
    			// console.log(vthis.topic.author.loginname);
                //console.log(vthis.topic);
    		}).catch(function(){
    			console.log('');
    		});
    	},
        updated(){
              // 
              var reg = /^http.*?/;
              $('.img_src').each(function(index){ 
                var res = reg.exec($(this).attr('src'));
                if(!res){
                  $(this).attr('src','http:'+$(this).attr('src'))
                }
            });
        }

	}
    
/*$(function(){
    //img src="//"
    //http
    var reg = /^http.*?/;
      $('img').each(function(index){
        console.log($(this)); 
        //
        var res = reg.exec($(this).attr('src'));
        if(!res){
            //console.log('http');

            //http
          $(this).attr('src','http:'+$(this).attr('src'))
        }
    });
})*/
</script>
<style scoped="">
    .main{
        text-align:left;
    }
  	.title{
        font-size:1.25rem;
        font-weight:700;

    }
    .changes {
        width:23.4375rem;
        height:2.5rem;
        padding:10px 0 6px 10px;
        font-size: 0.75rem;
        color: #838383;
    }
    .changes li{
        font-size:0.75rem;
        color:#838383;
        float:left;
        margin: 0 -12px;
        margin-right:20px;
    }
    .changes .dote::before{
        content:'';
    }
    .reply{
        border-bottom:1px solid #F0F0F0;
        padding:10px 0;
    }
    .reply_author:after{
        content:"";
        display:table;
        clear:both;
    }
    .reply_author img{
        width:1.875rem;
        height:1.875rem;
        margin-right:10px;
    }
    .reply_author a{
        float:left;
        margin:0 2px;
    }
    .reply_author a:nth-child(2){
        color:#000;
    }
    .reply_author span{
        float:right;
    }
    .reply_content{
        margin-top:10px;
    }
    .zan{
        margin-right:3px;
    }
</style>


